class CustomAccountCell {
  /**
   * Custom Row Cell for Configuration Page
   * @constructor
   * @param {Object} globalConfig - Global configuration.
   * @param {string} serviceName - Input service name.
   * @param {element} el - The element of the custom cell.
   * @param {Object} row - custom row object.
   * @param {string} field - The cell field name.
   */
  constructor (globalConfig, serviceName, el, row, field) {
    this.globalConfig = globalConfig
    this.serviceName = serviceName
    this.el = el
    this.row = row
    this.field = field
  }

  render (row) {
    let html = ''
    if (this.serviceName === 'aws_account' || this.serviceName === 'aws_private_account') {
      if (this.field === 'iam') {
        html = row.iam ? 'Yes' : 'No'
      } else if (this.field === 'sts_private_endpoint_url') {
        const STSPrivateEndpoint = row.sts_private_endpoint_url
        html = (STSPrivateEndpoint) || ('Not Provided')
      }
    }
    this.el.innerHTML = html
    return this
  }
}
export default CustomAccountCell
